import { useState } from "react";
import { Nav } from "../../navigation";
import s from "./header.module.scss";
import { ReactTyped } from "react-typed";

import ava from "./../../../app/assets/imgs/myphoto.png";
export const Header = () => {
  const [isFixed,setNavFixed]=useState(false)
  return (
    <header>
      
      <Nav isFixed={isFixed} setNavFixed={setNavFixed}/>
      <div className={`${s.wrapper} ${isFixed ? s.mtop : ""}`}>
        <div className={`${s.description} topline`}>
          <h3>,  </h3>
          <h1 className="headline">
            <span>
              <ReactTyped
                strings={[" ", "Frontend developer"]}
                typeSpeed={80}
                backSpeed={50}
                loop
              />
            </span>
          </h1>
          <p>
                 ,   
                 .
          </p>
          <div className={s.downloadSV}>
            <button className={s.button}>
              <a download={false} href="##">
                 CV
              </a>
            </button>
          </div>
        </div>
        <div className={`${s.ramka} bottomline`}>
          <img src={ava} alt="myPhoto" />
        </div>
      </div>
    </header>
  );
};
