import { SkillItem } from "../../../entities/skillItem/ui"
import s from "./skillsList.module.scss"
export const SkillsList=({title})=>{
    return <div className={s.box}>
      <h4 className={s.title}>{title}</h4>
      <div className={s.content}>
        <SkillItem widthBar={90} title={"HTML"}/>
        <SkillItem widthBar={80} title={"CSS"}/>
        <SkillItem widthBar={80} title={"SCSS"}/>
      </div>
    </div>
    
  
}